<template>
  <div>
    <a-card>
      <a-table
        rowKey="ID"
        :columns="columns"
        :pagination="pagination"
        :dataSource="commentList"
        bordered
        @change="handleTableChange"
      >
        <span slot="status" slot-scope="data">{{ data == 1 ? '' : '' }}</span>
        <template slot="action" slot-scope="data">
          <div class="actionSlot">
            <a-button
              type="primary"
              icon="edit"
              style="margin-right: 15px"
              @click="commentCheck(data.ID)"
            ></a-button>
            <a-button
              type="primary"
              icon="info"
              style="margin-right: 15px"
              @click="commentUncheck(data.ID)"
            ></a-button>
            <a-button
              type="danger"
              icon="delete"
              style="margin-right: 15px"
              @click="deleteComment(data.ID)"
            ></a-button>
          </div>
        </template>
      </a-table>
    </a-card>
  </div>
</template>
<script>
import day from 'dayjs'
const columns = [
  {
    title: 'ID',
    dataIndex: 'ID',
    width: '2%',
    key: 'id',
    align: 'center',
  },
  {
    title: '',
    dataIndex: 'CreatedAt',
    width: '10%',
    key: 'CreatedAt',
    align: 'center',
    customRender: (val) => {
      return val ? day(val).format('YYYYMMDD HH:mm') : ''
    },
  },
  {
    title: '',
    dataIndex: 'article_title',
    width: '7%',
    key: 'article_title',
    align: 'center',
  },
  {
    title: '',
    dataIndex: 'username',
    width: '7%',
    key: 'username',
    align: 'center',
  },
  {
    title: '',
    dataIndex: 'content',
    width: '20%',
    key: 'content',
    align: 'center',
  },
  {
    title: '',
    dataIndex: 'status',
    width: '7%',
    key: 'status',
    align: 'center',
    scopedSlots: { customRender: 'status' },
  },
  {
    title: '',
    width: '20%',
    key: 'action',
    align: 'center',
    scopedSlots: { customRender: 'action' },
  },
]
export default {
  data() {
    return {
      commentList: [],
      commentInfo: {
        status: 1,
      },
      pagination: {
        pageSizeOptions: ['5', '10', '20'],
        pageSize: 5,
        total: 0,
        showSizeChanger: true,
        showTotal: (total) => `${total}`,
      },
      columns,
      queryParam: {
        pagesize: 10,
        pagenum: 1,
      },
    }
  },
  created() {
    this.getCommentList()
  },
  methods: {
    // 
    async getCommentList() {
      const { data: res } = await this.$http.get('admin/comment/list', {
        params: {
          pagesize: this.queryParam.pagesize,
          pagenum: this.queryParam.pagenum,
        },
      })

      if (res.status !== 200) {
        if (res.status === 1004 || 1005 || 1006 || 1007) {
          window.sessionStorage.clear()
          this.$router.push('/login')
        }
        this.$message.error(res.message)
      }
      this.commentList = res.data
      this.pagination.total = res.total
    },

    // 
    handleTableChange(pagination, filters, sorter) {
      var pager = { ...this.pagination }
      pager.current = pagination.current
      pager.pageSize = pagination.pageSize
      this.queryParam.pagesize = pagination.pageSize
      this.queryParam.pagenum = pagination.current

      if (pagination.pageSize !== this.pagination.pageSize) {
        this.queryParam.pagenum = 1
        pager.current = 1
      }
      this.pagination = pager
      this.getCommentList()
    },

    // 
    commentCheck(id) {
      this.$confirm({
        title: '',
        content: '',
        onOk: async () => {
          const { data: res_status } = await this.$http.get(`comment/info/${id}`)
          if (res_status.data.status === 1) return this.$message.error('')
          const { data: res } = await this.$http.put(`admin/checkcomment/${id}`, {
            status: 1,
          })
          if (res.status != 200) return this.$message.error(res.message)
          this.$message.success('')
          this.getCommentList()
        },
        onCancel: () => {
          this.$message.info('')
        },
      })
    },

    // 
    commentUncheck(id) {
      this.$confirm({
        title: '',
        content: '',
        onOk: async () => {
          const { data: res_status } = await this.$http.get(`comment/info/${id}`)
          if (res_status.data.status === 2) return this.$message.error('')
          const { data: res } = await this.$http.put(`admin/uncheckcomment/${id}`, {
            status: 2,
          })
          if (res.status != 200) return this.$message.error(res.message)
          this.$message.success('')
          this.getCommentList()
        },
        onCancel: () => {
          this.$message.info('')
        },
      })
    },

    // 
    deleteComment(id) {
      this.$confirm({
        title: '',
        content: '',
        onOk: async () => {
          const { data: res } = await this.$http.delete(`admin/delcomment/${id}`)
          if (res.status != 200) return this.$message.error(res.message)
          this.$message.success('')
          this.getCommentList()
        },
        onCancel: () => {
          this.$message.info('')
        },
      })
    },
  },
}
</script>
